module.exports = [[module.id, "/* You can add global styles to this file, and also import other style files */\n\nz\n/*  Radio Button Start  */\n\n.pure-material-radio {\n    z-index: 0;\n    position: relative;\n    display: inline-block;\n    color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.87);\n    font-size: 13px;\n    line-height: 1.5;\n}\n\n/* Input */\n\n.pure-material-radio>input {\n    appearance: none;\n    -moz-appearance: none;\n    -webkit-appearance: none;\n    z-index: -1;\n    position: absolute;\n    left: -10px;\n    top: -8px;\n    display: block;\n    margin: 0;\n    border-radius: 50%;\n    width: 40px;\n    height: 40px;\n    background-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.6);\n    outline: none;\n    opacity: 0;\n    transform: scale(1);\n    pointer-events: none;\n    transition: opacity 0.3s, transform 0.2s;\n}\n\n/* Span */\n\n.pure-material-radio>span {\n    display: inline-block;\n    width: 100%;\n    cursor: pointer;\n    margin-right: 6px;\n}\n\n/* Circle */\n\n.pure-material-radio>span::before {\n    content: \"\";\n    display: inline-block;\n    box-sizing: border-box;\n    margin: 2px 4px 2px 0;\n    border: solid 2px;\n    /* Safari */\n    border-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.6);\n    border-radius: 50%;\n    width: 18px;\n    height: 18px;\n    vertical-align: top;\n    transition: border-color 0.2s;\n}\n\n/* Check */\n\n.pure-material-radio>span::after {\n    content: \"\";\n    display: block;\n    position: absolute;\n    top: 2px;\n    left: 0;\n    border-radius: 50%;\n    width: 8px;\n    height: 8px;\n    background-color: #268b45;\n    transform: translate(5px, 5px) scale(0);\n    transition: transform 0.2s;\n}\n\n/* Checked */\n\n.pure-material-radio>input:checked {\n    background-color: rgb(var(--pure-material-primary-rgb, 33, 150, 243));\n}\n\n.pure-material-radio>input:checked+span::before {\n    border-color: #268b45;\n}\n\n.pure-material-radio>input:checked+span::after {\n    transform: translate(5px, 5px) scale(1);\n}\n\n/* Hover, Focus */\n\n.pure-material-radio:hover>input {\n    opacity: 0.04;\n}\n\n.pure-material-radio>input:focus {\n    opacity: 0.12;\n}\n\n.pure-material-radio:hover>input:focus {\n    opacity: 0.16;\n}\n\n/* Active */\n\n.pure-material-radio>input:active {\n    opacity: 1;\n    transform: scale(0);\n    transition: transform 0s, opacity 0s;\n}\n\n.pure-material-radio>input:active+span::before {\n    border-color: rgb(var(--pure-material-primary-rgb, 33, 150, 243));\n}\n\n/* Disabled */\n\n.pure-material-radio>input:disabled {\n    opacity: 0;\n}\n\n.pure-material-radio>input:disabled+span {\n    color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), 0.38);\n    cursor: initial;\n}\n\n.pure-material-radio>input:disabled+span::before {\n    border-color: currentColor;\n}\n\n.pure-material-radio>input:disabled+span::after {\n    background-color: currentColor;\n}\n\n/*  Radio Button End */\n\n/* Text area Background Color start */\n\ntextarea.form-control {\n    height: auto;\n    background-color: #f6f9f7;\n    border: none;\n}\n\n.form-control:focus {\n    color: #495057;\n    background-color: #f6f9f7;\n    border-color: transparent;\n    outline: 0;\n    box-shadow: none;\n}\n\n/*  Text area Background Color End */\n\n/*  Button ripple Effect Start */\n\n.ripple {\n    position: relative;\n    overflow: hidden;\n}\n\n.ripple:before {\n    border-radius: 50%;\n    background-color: rgba(255, 255, 255, 0.6);\n    content: '';\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    width: 0;\n    height: 0;\n}\n\n.ripple:focus:before {\n    transition: all 0.5s ease-out;\n    opacity: 0;\n    width: 160px;\n    height: 160px;\n    margin-top: -80px;\n    margin-left: -80px;\n}\n\n/* Button ripple Effect End */\n\n/* Remove focus form all buttons start */\n\n.form-control:focus {\n    border: 1px solid #2C8E4A;\n    box-shadow: none;\n    outline: none;\n}\n\n.btn-success.focus,\n.btn-success:focus {\n    box-shadow: transparent;\n    box-shadow: none;\n}\n\n/*  Remove focus form all buttons End */\n\n/* Error Message  */\n\n.invalid-feedback {\n    display: none;\n    width: 100%;\n    margin: -18px 0 0 12px;\n    font-size: 80%;\n    color: #dc3545;\n}\n\n/*  Error Message  */\n\n/*  Corousel  */\n\n.owl-nav button {\n    height: 35px;\n    width: 35px;\n    background: #FFF !important;\n    color: #2C8E4A !important;\n    margin-right: 20px;\n    position: relative;\n    outline: none;\n    border: 1px solid #2C8E4A !important;\n    border-radius: 3px !important;\n}\n\n.owl-nav i {\n    font-size: 25px !important;\n    margin-top: 4px;\n}\n\n.owl-nav button:hover {\n    height: 35px;\n    width: 35px;\n    background: #2C8E4A !important;\n    color: #FFF !important;\n    margin-right: 20px;\n    position: relative;\n    outline: none;\n    border: 1px solid #2C8E4A !important;\n}\n\n.owl-nav {\n    position: absolute;\n    top: -50px;\n    right: -15px;\n}\n\n.owl-slider {\n    margin-top: 50px;\n}\n\n.owl-carousel .owl-nav button.owl-next,\n.owl-carousel button.owl-dot {\n    border-top-right-radius: 6px !important;\n    border-bottom-right-radius: 6px !important;\n}\n\n.owl-carousel .owl-nav button.owl-prev,\n.owl-carousel button.owl-dot {\n    border-top-left-radius: 6px !important;\n    border-bottom-left-radius: 6px !important;\n}\n\n/* Validation */\n\n.error {\n    color: red;\n    font-size: 12px;\n}\n\n/* Alert */\n\n.alert-message {\n    margin: 20px 0;\n    padding: 15px 10px 1px 10px;\n    border-left: 3px solid #eee;\n    border-bottom: none;\n    border-right: none;\n    border-top: none;\n    border-radius: 0px;\n}\n\n.alert-message h4 {\n    margin-top: 0;\n    margin-bottom: 5px;\n}\n\n.alert-message-success {\n    background-color: #F4FDF0;\n    border-color: #3C763D;\n    text-align: center;\n}\n\n.alert-message-success h4 {\n    color: #3C763D;\n    font-size: 16px;\n}\n\n.alert-dismissible .close {\n    position: absolute;\